<template>
    <div class="panel" :style="{width: displayWidth, 'min-height': displayHeight, zoom: displayZoom}">
        <div class="panel-heading" v-if="hasHeaderSlot"><h1><slot name="header"></slot></h1></div>
        <div class="panel-body" :style="{height: bodyHeight}" v-if="hasBodySlot"><slot name="body"></slot></div>
        <div class="table-responsive" v-if="hasTableSlot"><slot name="table_responsive"></slot></div>
        <div class="panel-footer" v-if="hasFooterSlot"><slot name="footer"></slot></div>
    </div>
</template>

<script>
export default {
    name: 'panel',

    props: ['config'],

    computed: {
        displayWidth () {
            return (this.config.width ? this.config.width : '100%');
        },

        displayHeight () {
            return (this.config.height ? this.config.height : '50px');
        },

        bodyHeight () {
            return (this.config.bodyHeight !== undefined ? this.config.bodyHeight : '100%');
        },

        displayZoom () {
            return (this.config.zoom ? this.config.zoom : '1');
        },

        hasHeaderSlot () {
            return this.$slots.header;
        },

        hasBodySlot () {
            return this.$slots.body;
        },

        hasTableSlot () {
            return this.$slots.table_responsive;
        },

        hasFooterSlot () {
            return this.$slots.footer;
        }
    },

    data () {
        return {
        };
    }
};
</script>

<style lang="scss" scoped>
@import '~sass/_variables.scss';
@import '~sass/panel.scss';
@import '~sass/tooltip.scss';

.panel /deep/ {
    border-radius: 0;
    border: none;
    box-shadow: 0px 0px 40px -30px rgba(0, 0, 0, 0.75);
    margin: 0.5em 0;
    padding: 1em;

    .clickable:hover {
        background-color: lightgray;
        cursor: pointer;
    }

    a {
        &.unclickable {
            cursor: default;
            pointer-events: none;
        }
    }

    &.unclickable a {
        cursor: default;
        pointer-events: none;
    }
}
</style>



