import Vue from 'vue';
import Vuex from 'vuex';

Vue.use(Vuex);

const store = new Vuex.Store({
    strict: true,
    getters: {
        unloggedMode: state => {
            return state.unloggedMode;
        },
        baseUser: state => {
            return state.baseUser;
        },
        globalOptions: state => {
            return state.globalOptions;
        },
        httpInitialized: state => {
            return state.httpInitialized;
        }
    },

    modules: {
    },

    state: {
        unloggedMode: false,
        baseUser: null,
        httpInitialized: false,
        globalOptions: {}
    },

    mutations: {
        setUnloggedMode (state) {
            state.unloggedMode = true;
        },

        setBaseUser (state, us) {
            state.baseUser = us;
        },

        initSystem (state, options) {
            if (state.globalOptions.env === undefined && typeof options === 'object' && options !== null) {
                state.globalOptions = options;
            }
        },

        httpInitialized (state, newValue) {
            state.httpInitialized = newValue;
        }
    }
});

export default store;



