<template>
    <div :id="id_div" class="asnet_comp_root" :style="{width: width + 'px', height: height + 'px'}">
        <keep-alive exclude="loading-comp">
            <component @display-sized="resizeme" :is="comp" :config="config" ref="active_comp"></component>
        </keep-alive>
    </div>
</template>

<script>
// import first here even not used to compile axios in main js file. So in
// components using axios, when importing it, the chunk js file (for async
// component) not contains axios again but use it from main js file (reduce
// component's chunk files size)
// TODO check webpack handles common chunks correctly so that we can safely
// remove the following import
// import httpAPI from '@/httpapi';
import LoadingComp from '@/components/LoadingComp';
import LoadingErrorComp from '@/components/LoadingErrorComp';
import bus from '@/event_bus';
import store from '@/store';

export default {
    name: 'app',

    props: ['comp', 'id_div', 'config'],

    methods: {
        resizeme (w, h) {
            this.width = w;
            this.height = h;
        },
        userConnected (e, extra) {
            if (this.$refs.active_comp) {
                this.$refs.active_comp.$emit('user-connected', e);
            }
        },
        userUnlogged (e, extra) {
            if (this.$refs.active_comp) {
                this.$refs.active_comp.$emit('user-unlogged', e);
            }
        },
        initSystem (options) {
            store.commit('initSystem', options);
        },
    },

    components: {
        'bob': () => {
            return {
                component: System.import(/* webpackChunkName: "bob" */'./components/Bob'),
                loading: LoadingComp,
                delay: 0
            };
        },
        'panel': () => {
            return {
                component: System.import(/* webpackChunkName: "panel" */'./components/Panel')
            };
        },
        'v-card': () => {
            return {
                component: System.import(/* webpackChunkName: "v_card" */'./components/Profile/VCard'),
                loading: LoadingComp,
                delay: 2,
                timeout: 1000,
            };
        },
        'user-activities': () => {
            return {
                component: System.import(/* webpackChunkName: "user_activities" */'./components/Profile/Activities'),
                loading: LoadingComp,
                delay: 0
            };
        },
        'avatarbuiltin-selector': () => {
            return {
                component: System.import(/* webpackChunkName: "avatarbuiltin_selector" */'./components/Profile/Avatar/AvatarBuiltinSelector'),
                loading: LoadingComp,
                delay: 0
            };
        },
        'avatar-handler': () => {
            return {
                component: System.import(/* webpackChunkName: "avatar_handler" */'./components/Profile/AvatarHandler'),
                loading: LoadingComp,
                delay: 0
            };
        },
        'buddies-collection': () => {
            return {
                component: System.import(/* webpackChunkName: "buddies_collection" */'./components/Profile/Buddies/Collection'),
                loading: LoadingComp,
                delay: 0
            };
        },
        'buddies-activities': () => {
            return {
                component: System.import(/* webpackChunkName: "buddies_activities" */'./components/Profile/Buddies/Activities'),
                loading: LoadingComp,
                delay: 0
            };
        },
        'ranking': () => {
            return {
                component: System.import(/* webpackChunkName: "ranking" */'./components/Ranking'),
                loading: LoadingComp,
                delay: 0
            };
        },
        'my-ranking': () => {
            return {
                component: System.import(/* webpackChunkName: "my-ranking" */'./components/MyRanking'),
                loading: LoadingComp,
                delay: 2,
                timeout: 1000,
                error: LoadingErrorComp
            };
        },
        'relationship-button': () => {
            return {
                component: System.import(/* webpackChunkName: "relationship-button" */'./components/Buttons/Relationship'),
                loading: LoadingComp,
                delay: 0
            };
        },
        'game-achievements': () => {
            return {
                component: System.import(/* webpackChunkName: "game_achievements" */'./components/Game/Achievements'),
                loading: LoadingComp,
                delay: 0
            };
        },
        'game-histories': () => {
            return {
                component: System.import(/* webpackChunkName: "game_histories" */'./components/Game/Histories'),
                loading: LoadingComp,
                delay: 0
            };
        },
        'game-solitaire': () => {
            return {
                component: System.import(/* webpackChunkName: "game_solitaire" */'./components/Game/Solitaire'),
                loading: LoadingComp,
                delay: 0
            };
        },
    },
    data () {
        return {
            width: null,
            height: null
        };
    },
    mounted () {
        bus.$once('user-connected', this.userConnected);
        bus.$once('user-unlogged', this.userUnlogged);
        bus.$once('init-all', this.initSystem);
    }
};
</script>

<style lang="scss" scoped>
@import '~sass/variables';

.asnet_comp_root /deep/ {
    font-family: 'Helvetica Neue', Helvetica, Arial, sans-serif;
    // -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    color: $font-color-base;
    h1, h2, h3, h4, h5, h6 {
        font-weight: $header-font-weight-base;
    }
}
</style>



